body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body,#root {
  width: 100%;
  height: 100%;
}
.App {
  text-align: center;
  width: 100%;
  height: 100%;
  position: relative;
}

.App .calculator-wrap {
  background: #afafaf;
  width: 500px;
  padding: 30px 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.container {
  -webkit-flex-grow: 4;
          flex-grow: 4;
}
.content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.content .number-input {
  width: 100px;
  height: 30px;
}

.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 40px;
}
.footer button {
  width: 80px;
  height: 25px;
  border-radius: 15px;
}

.aside {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-align: left;
}
.aside p span {
  font-size: 60px;
}

